.footer__main{
	height: 240px;
	background: black;
}

/*
    -------------------------
    |              			|
    |       SITEMAP         |
    |                   	|
    -------------------------
*/
.sitemap{
	display: inline-block;
	width: 17.5%;
	height: 180px;
	float: left;
	color: white;
	margin-right: 30px;
}
.sitemap:last-child{margin: 0}

.sitemap__title{
	display: inline-block;
	width: 100%;
	padding: 20px 0;
	color: white;
	text-transform: uppercase;
	font-weight: normal;
	border-bottom: 2px solid #A51C30;
}

.sitemap__text{
	display: inline-block;
	padding: 15px 0;
	list-style: none;
	font-size: 0.9rem;
	font-weight: lighter;
}
.sitemap__text a{color: white}
.sitemap__text a:hover{color: #6B6767}
.sitemap__text li{padding-bottom: 10px}

/*
    -------------------------
    |               		|
    |       ALLRIGHT        |
    |                   	|
    -------------------------
*/
.allright{
	color: white;
	background: #1E1E1E;
}

.allright span{
	display: inline-block;
	padding: 20px 0;
	padding-right: 6px;
}

/*.allright span:hover{color: #6B6767}*/
.allright span:after{content: ' |';color: #6B6767;}
.allright span:last-child:after{content: ''}

.allright__right{
	position: relative;
	margin-left: 30px;
	color: #6B6767;
}
.allright__right b{
	position: relative;
	top: 3px;
}
/*.allright__coder:hover{color: white !important}*/
.allright__coder a{color: #8A2634;text-decoration: underline;}
.allright__coder a:hover{text-decoration: none}


