.app__img{
	display: inline-block;
	width: 100%;
	height: 400px;
	float:left;
	background-color: #E6E5E3;
}
.app__img .wrapper{
	background-position: 50% 100%;
	background-size: cover;
}

.app__content{
	display: inline-block;
	width: 100%; 
	color: #222;
	background: rgb(248, 248, 248);
	-webkit-box-shadow: 0 -6px 20px rgba(0, 0, 0, 0.2);
	        box-shadow: 0 -6px 20px rgba(0, 0, 0, 0.2);
}

.app__content > div{
	border-left: 2px solid #E6E5E3;
	border-right: 2px solid #E6E5E3;
}

.app__title{
	padding: 20px 0;
	font-size: 2.2rem;
	font-weight: normal;
	text-align: center;
}
.app__text{
	padding: 0 10px 30px 10px;
	line-height: 24px;
	word-spacing: 2px;
	font-size: 1.05rem;
}
.app__text b{font-size: 1.3rem}
.app__text b:nth-child(1){font-family: 'Chewy'}
.app__text span:first-child{
	display: block;
	padding-bottom: 15px;
}


