.notFound{
	height: 90vh;
	min-height: 600px;
	background: url(background.jpg) no-repeat;
	background-size: cover;
}

.notFound__title{
	position: absolute;
	top: 50%;
	left: 50%;
	color: #222;
	-webkit-transform: translate(-47%, -500%);
	    -ms-transform: translate(-47%, -500%);
	        transform: translate(-47%, -500%);
	font-size: 5rem;
	line-height: 1rem;
	letter-spacing: 2rem;
}

.notFound__img span{
	position: absolute;
	width: 128px;
	height: 128px;
	background-size: cover;
	background-repeat: no-repeat;
}

.notFound__seaHorse{
	top: 55%;
	left: 70%;
	background-image: url(seaHorse.png);
}
.notFound__nemo{
	top: 25%;
	left: 15%;
	background-image: url(nemo.png);
}
.notFound__friend{
	top: 25%;
	left: 70%;
	background-image: url(friend.png);
}
.notFound__star{
	top: 12%;
	left: 40%;
	background-image: url(star.png);
}
.notFound__sharp{
	width: 200px !important;
	height: 200px !important;
	top: 50%;
	left: 6%;
	background-image: url(sharp.png);
}


