.jury{min-height: 1200px}
.jury__title{padding-bottom: 60px}
.jury__item{
	position: relative;
	display: inline-block;
	width: 31.33%;
	height: 480px;
	padding: 3px;
	margin: 0 3% 26px 0;
	-webkit-box-shadow: 0 16px 40px 0 rgba(76,93,110,0.08), 0 4px 8px rgba(76,93,110,0.1);
	        box-shadow: 0 16px 40px 0 rgba(76,93,110,0.08), 0 4px 8px rgba(76,93,110,0.1);
}
.jury__item:nth-child(3n+3) {margin-right: 0}
.jury__item:before{
	content: '';
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 70px;
	z-index: -1;
	background: -webkit-linear-gradient(45deg, rgb(241, 125, 139), rgb(221, 155, 189));
	background: -o-linear-gradient(45deg, rgb(241, 125, 139), rgb(221, 155, 189));
	background: linear-gradient(45deg, rgb(241, 125, 139), rgb(221, 155, 189));
}

.jury__img{
	position: relative;
	left: 50%;
	display: inline-block;
	width: 140px;
	height: 140px;
	margin: 20px 0 0 -70px;
	border-radius: 50%;
	background-size: cover;
}

.jury__name{
	position: absolute;
	top: 190px;
	left: 0;
	right: 0;
	color: #A51C30;
	font-size: 1.03rem;
	text-align: center;
	font-weight: bold;
}
.jury__right{
	position: absolute;
	top: 215px;
	left: 0;
	right: 0;
	color: #6B6767;
	font-size: 0.9rem;
	font-style: italic;
	text-align: center;
}
.jury__position{
	position: absolute;
	top: 260px;
	left: 0;
	right: 0;
	padding: 0 10px;
	text-align: center;
	line-height: 23px;
}
.jury__location{
	position: absolute;
	bottom: 5px;
	left: 10px;
}


