.olimpiad{min-height: 90vh}

.olimpiad__content{
	width: 86%;
	margin: 40px auto 0 auto;
}

.olimpiad__item{
	position: relative;
	display: inline-block;
	width: 48.5%;
	margin: 0 3% 25px 0;
}
.olimpiad__item:nth-child(2n+2){margin-right: 0}

.olimpiad__img{
	display: inline-block;
	width: 160px;
	height: 100px;
	background-size: cover;
}

.olimpiad__name{
	position: absolute;
	top: 0;
	left: 170px;
	color: #222;
	padding: 0;
	font-weight: bold;
	font-size: 1.1rem;
}
.olimpiad__date{
	position: absolute;
	bottom: 23px;
	left: 170px;
	color: #6B6767;
	font-size: 0.7rem;
	letter-spacing: 1px;
}

.olimpiad__href{
	position: absolute;
	bottom: 2px;
	left: 170px;
	color: #A51C30;
	text-decoration: underline;
}
.olimpiad__href:hover{text-decoration: none}
.olimpiad__href:after{
	content: '';
	position: relative;
	display: inline-block;
	top: 11px;
	left: 7px;
	width: 24px;
	height: 24px;
	background: url(link.png) no-repeat;
}


