@charset "UTF-8";
@import url("https://fonts.googleapis.com/css?family=Roboto:300,400,700");
@import url('https://fonts.googleapis.com/css?family=Merriweather:300,400,700&subset=cyrillic');
@import url('https://fonts.googleapis.com/css?family=Chewy');

*, *:before, *:after {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	padding: 0;
	margin: 0;
}

a {
	color: #003366;
	text-decoration: none;
}

html {
	color: #222;
	font-size: 16px;
	font-family: 'Merriweather', 'Roboto', sans-serif;
}

.wrapper {
	position: relative;
	max-width: 980px;
	height: 100%;
	margin: 0 auto;
}

h2{
	padding: 60px 0 40px;
	font-size: 2.4rem;
	color: #444;
	font-weight: normal;
}

h3{
	padding-bottom: 40px;
	text-align: center;
	font-size: 1.6rem;
	color: #444;
	color: #8A2634;
	font-weight: normal;
}

/*
	-------------------------
	|						|
	|		SameType 		|
	|						|
	-------------------------
*/
.sameType{
	min-height: 700px;
}
.sameType__item{
	position: relative;
	display: inline-block;
	width: 48.5%;
	margin: 0 3% 16px 0;
	border: 1px solid #bac5c6 !important;
}
.sameType__item:nth-of-type(2n+2) {margin-right: 0}
.sameType__title2{text-align: left !important}

.sameType__img{
	display: inline-block;
	width: 200px;
	height: 120px;
	float: left;
	background-repeat: no-repeat;
	background-size: cover;
}

.sameType__name{
	position: absolute;
	top: 5px;
	left: 210px;
	text-align: center;
	color: #8A2634;
	line-height: 18px;
	font-size: 1.1rem;
	font-weight: bold;
	text-decoration: underline;
}
.sameType__item:hover .sameType__name{text-decoration: none}









/*
	-------------------------
	|						|
	|		SameChild 		|
	|						|
	-------------------------
*/
.sameChild{min-height: 90vh}

.sameChild__item{
	position: relative;
	display: inline-block;
	width: 31.7%;
	height: 262px;
	margin: 0 2.4% 25px 0;
	-webkit-box-shadow: 0 -10px 30px 0 rgba(76, 93, 110, 0.07), 0 4px 8px rgba(76, 93, 110, 0.1);
	        box-shadow: 0 -10px 30px 0 rgba(76, 93, 110, 0.07), 0 4px 8px rgba(76, 93, 110, 0.1);
	border: 1px solid #bac5c6 !important;
}
.sameChild2__item{margin-top: 30px}
.sameChild__item:nth-child(3n+3) {margin-right: 0}
.sameChild__name{
	position: absolute;
	top: 5px;
	left: 5px;
	right: 5px;
	text-align: center;
	color: #8A2634;
	font-size: 1.1rem;
}
.sameChild__school{
	position: absolute;
	bottom: 0;
	right: 5px;
	font-size: 0.9rem;
	font-style: italic;
}

.sameChild__place{
	position: absolute;
	top: -35px;
	left: 50%;
	width: 80px;
	margin-left: -40px;
	font-size: 1.2rem;
	color: #8A2634;
	text-align: center;
}

.sameChild__img{
	position: absolute;
	top: 28px;
	left: 0;
	right: 0;
	height: 190px;
	background-size: cover;
	background-position: 50% 50%;
}

.sameChild__buttonZoom{
	content: '';
	position: absolute;
	top: 5px;
	right: 5px;
	width: 32px;
	height: 32px;
	opacity: 0;
	border-radius: 50%;
	cursor: pointer;
	-webkit-transition: .3s;
	-o-transition: .3s;
	transition: .3s;
	background: url(zoom.png) no-repeat;
}

.sameChild__img:hover .sameChild__buttonZoom{opacity: 0.6}
.sameChild__buttonZoom:hover{opacity: 0.9 !important}
.sameChild__notFound{
	text-align: center;
	color: #A51C30;
}

.popup{
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	z-index: 5;
	background: 50% 50% rgba(0, 0, 0, 0.85) no-repeat;
	background-size: 550px;
}

.popup__button{
	position: absolute;
	top: 0;
	width: 50%;
	height: 100%;
	z-index: 5;
	opacity: 0.4;
	cursor: pointer;
	-webkit-transition: 0.15s;
	-o-transition: 0.15s;
	transition: 0.15s;
}

.popup__buttonLeft{
	left: 0;
	background: url(./left.png) 1% 50% no-repeat;
}
.popup__buttonRight{
	right: 0;
	background: url(./right.png) 99% 50% no-repeat;
}

.popup__buttonClose{
	position: absolute;
	top: 15px;
	right: 15px;
	width: 24px;
	height: 24px;
	background: url(./close.png);
	opacity: 0.4;
	z-index: 6;
	-webkit-transition: 0.15s;
	-o-transition: 0.15s;
	transition: 0.15s;
	cursor: pointer;
}
.popup__button:hover{opacity: 1}
.popup__buttonClose:hover{opacity: 1}

.popup__count{
	position: absolute;
	bottom: 5px;
	left: 50%;
	width: 100px;
	margin-left: -50px;
	font-family: Roboto;
	font-size: 1.2rem;
	color: white;
	text-align: center;
}




















/*
	-------------------------
	|						|
	|		ADAPTiVE		|
	|						|
	-------------------------
*/
@media screen and (max-width: 980px) {
	.header__logo{width: 100%}
	.header__nav{
		width: 100%;
		float: none;
	}
	.header__nav > li > a{padding: 15px 0}

	.up{
		left: 10px;
		bottom: 10px;
		width: 40px;
		height: 40px;
		border-radius: 50%;
		background: #8A2634;
	}
	.up:before{display: block}
	.up__text{display: none !important}

	.same__background{height: 200px}

	.sameChild__item{
		width: 47%;
		height: 302px;
	}
	.sameChild__img{height: 230px}
	.sameChild__item:nth-child(2n+2) {margin-right: 5px}
	.sameChild__item:nth-child(2n+1) {margin: 0 2.4% 25px 5px}

	.competitionChild__item{width: 49.2%}
	.competitionChild__item:nth-child(2n+2){margin-right: 0}
	.competitionChild__img{height: 240px}

	.sameType__item{
		width: 98.3%;
		margin: 0 5px 16px 5px;
	}


	.olimpiad__item{
		width: 96%;
		margin: 0 0 25px 10px;
	}


	.totalChild__item{
		position: relative;
		display: block;
		top: 0 !important;
		left: 0 !important;
		width: 450px;
		height: 317px;
		margin: 50px auto !important;
	}
	.totalChild__img{height: 230px}
	.totalChild__name{top: 235px}


	.jury__item{
		height: 450px;
		width: 47.5%;
		margin: 0 2% 26px 5px;
	}
	.jury__item:nth-child(2n+2) {margin-right: 5px}
	.jury__item:nth-child(3n+3) {margin-right: 2%}

	.footer__main{height: 360px}
	.sitemap{
		width: 30.67%;
		padding-left: 10px;
	}
	.sitemap:nth-child(3){margin: 0}
}
@media screen and (max-width: 768px) {
	html {font-size: 14px}

	.header__nav > li > a{padding: 10px 0}
	.header__nav2 > li > a{padding: 6px}
	.header__nav a:hover:after{width: 0}

	.sameChild__item{height: 248px}
	.sameChild__img{height: 180px}

	.competitionChild__img{height: 180px}

	.totalChild__item{height: 307px}
	.totalChild__item:before{margin-left: -31px}

	.jury__item{
		display: block;
		width: 370px;
		height: 440px;
		margin: 25px auto !important;
	}

	.footer__main{height: 560px}
	.sitemap__title{padding: 15px 0}
	.sitemap{
		width: 48.38%;
		margin-right: 15px;
	}
	.sitemap:nth-child(2n+2){margin: 0}
	.sitemap:nth-child(3){margin-right: 15px}
}
@media screen and (max-width: 600px) {
	html {font-size: 11px}

	h2{padding: 40px 0 20px}
	h3{padding-bottom: 20px}

	.header__logo{height: 50px}
	.header__nav > li > a{padding: 10px 0}
	.header__nav2{width: 160px}

	.sameChild__item{height: 208px}
	.sameChild__img{
		height: 155px;
		top: 23px;
	}

	.popup{background-size: 80%}

	.competitionChild__img{height: 140px}

	.olimpiad__img{height: 82px}

	.sameType__img{
		width: 180px;
		height: 90px;
	}
	.sameType__name{left: 190px}

	.totalChild__item{
		width: 290px;
		height: 207px;
	}
	.totalChild__img{height: 145px}
	.totalChild__name{top: 147px}
	.totalChild__item:before{
		top: -20px;
		margin-left: -25px;
	}

	.jury__item{
		width: 290px;
		height: 335px;
	}
	.jury__item:before{height: 50px}
	.jury__img{
		width: 100px;
		height: 100px;
		margin: 10px 0 0 -50px;
	}
	.jury__name{top: 125px}
	.jury__right{top: 145px}
	.jury__position{top: 175px}

	.footer__main{display: none}
}
@media screen and (max-width: 480px) {
	.container{padding-top: 50px}

	.header{
		position: fixed;
		width: 100%;
	}
	.header__logo{width: 160px}
	.header__mobnav{display: inline-block}
	.header__nav li{
		width: 100%;
		text-align: left;
	}
	.header__nav > li > a{padding-left: 5px}
	.header__nav2{
		position: relative;
		width: 100%;
		-webkit-box-shadow: none;
		        box-shadow: none;
		border: none;
		display: none;
	}
	.header__nav2 > li > a{padding-left: 30px}

	.app__img{height: 200px}

	.sameChild__item{
		display: block;
		width: 290px;
		height: 198px;
		margin: 40px auto !important;
	}
	.sameChild__place{top: -28px}
	.sameChild__img{height: 140px}
	.sameChild__school{bottom: 3px}

	.competitionChild__item{
		display: block;
		width: 99%;
		margin: 10px auto !important;
	}
	.competitionChild__img{height: 230px}
	.competitionChild__name{height: 35px}

	.olimpiad__content{width: 100%}

	.same__item{margin: 0 0 25px 20px}
	.same__itemLighter{margin-left: 20px}
	.same__list0 li{margin-left: 25px}

	.sameType__item{width: 96.5%}

	.allright span{
		width: 100%;
		padding: 0;
		padding-top: 15px;
		margin: 0;
		text-align: center;
	}
	.allright span:last-child{padding-bottom: 15px}
	.allright span:after{content: ''}
}
@media screen and (max-width: 320px) {
	.sameChild__item{width: 100%}

	.competitionChild__img{height: 150px}

	.olimpiad__item{width: 93%}
	.olimpiad__img{
		width: 100%;
		height: 130px;
	}
	.olimpiad__name{position: static}
	.olimpiad__date{
		position: static;
		display: inline-block;
		width: 100%;
		padding-top: 20px;
	}
	.olimpiad__href{
		position: static;
		display: inline-block;
		width: 100%;
	}

	.sameType__item{width: 97%}
	.sameType__img{
		width: 100%;
		height: 140px;
	}
	.sameType__name{
		position: static;
		display: inline-block;
		padding: 3px;
	}

	.totalChild__item{width: 100%}

	.jury__item{width: 260px}
}


