.competitionChild{min-height: 90vh}

.competitionChild__item{
	display: inline-block;
	width: 32.2%;
	padding: 3px;
	margin: 0 1.6% 16px 0;
	border: 1px solid #bac5c6 !important;
}
.competitionChild__item:nth-child(3n+3) {margin-right: 0}

.competitionChild__img{
	display: inline-block;
	width: 100%;
	height: 200px;
	background-size: cover;
	overflow: auto;
}
.competitionChild__img p{
	padding-bottom: 16px;
	font-size: 0.85rem;
}
.competitionChild__img p:last-child{padding: 0}

.competitionChild__name{
	display: inline-block;
	width: 100%;
	height: 50px;
	text-align: center;
	color: #8A2634;
	font-size: 1.1rem;
	font-weight: bold;
}
.competitionChild__school{
	display: inline-block;
	width: 100%;
	text-align: right;
	font-size: 0.9rem;
}


