@import url('https://fonts.googleapis.com/css?family=Chewy');

.header{
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    background-color: white;
    -webkit-box-shadow: 0 4px 4px rgba(0, 0, 0, 0.2);
            box-shadow: 0 4px 4px rgba(0, 0, 0, 0.2);
    z-index: 3;
}

.header__logo{
    position: relative;
    display: inline-block;
    width: 220px;
    height: 60px;
    line-height: 60px;
    text-align: center;
    color: black;
    font-size: 2.1rem;
    font-family: 'Chewy', 'Merriweather', 'Roboto', sans-serif;
}

.header__mobnav{
    position: absolute;
    top: 14px;
    right: 10px;
    display: inline-block;
    width: 24px;
    height: 24px;
    background: url(./menu.png);
    display: none;
    cursor: pointer;
}

/*
    ---------------------
    |                   |
    |       NAv1        |
    |                   |
    ---------------------
*/
.header__nav{
    width: 700px;
    list-style: none;
    float: right;
}
.header__nav li{
    position: relative;
    display: inline-block;
    width: 20%;
    text-align: center;
}
.header__nav a{
    display: inline-block;
    width: 100%;
    padding: 20px 0;
    color: #A51C30;
    background: white;
}
.header__nav span{
    position: relative;
    top: 5px;
    left: 8px;
    display: inline-block;
    border: 6px solid transparent;
    border-top: 6px solid #A51C30;
}

.header__nav a:after{
    content: '';
    position: absolute;
    bottom: 0;
    left: 50%;
    width: 0;
    height: 4px;
    background: #8A2634; /*A51C30 8A2634*/
}

.header__nav a:hover:after{width: 100%;left: 0;-webkit-transition: 0.4s;-o-transition: 0.4s;transition: 0.4s;}
.header__nav > li:hover .header__nav2{display: block}

/*
    ---------------------
    |                   |
    |       NAv2        |
    |                   |
    ---------------------
*/
.header__nav2{
    position: absolute;
    top: 100%;
    left: 0;
    width: 200px;
    display: none;
    border: 1px solid rgba(0, 0, 0, 0.3);
    border-top: none;
    list-style: none;
    -webkit-box-shadow: 0 3px 10px rgba(0, 0, 0, 0.4);
            box-shadow: 0 3px 10px rgba(0, 0, 0, 0.4);
}
.header__nav2 a{padding: 10px}
.header__nav2 a:hover:after{width: 0}
.header__nav2 a:hover{background: #A51C30;color: white;}
.header__nav2 li{width: 100%;text-align: left;}


