.up{
	position: fixed;
	bottom: 0;
	left: 0;
	width: 180px;
	height: 100%;
	-webkit-transition: 0.4s;
	-o-transition: 0.4s;
	transition: 0.4s;
	z-index: 2;
	cursor: pointer;
}

.up:hover{
	background-color: #8A2634;
	color: white;
}
.up:hover .up__text{opacity: 1}

.up:before{
	content: '';
	position: absolute;
	display: none;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: url(./up.png) 50% 50% no-repeat;
}

.up__text{
	display: inline-block;
	width: 100%;
	padding: 70px 0 0 10px;
	text-align: center;
	font-weight: bold;
	opacity: 0.5;
}


