.same{min-height: 90vh}
.same__background{
	display: inline-block;
	width: 100%;
	height: 320px;
	background-position: 0 65%;
	background-size: cover; 
}

.same__item{margin: 0 0 40px 100px}

.same__itemLighter{
	margin: 0 0 10px 100px;
	color: #A51C30;
}
.same__itemLighter .same__type{
	font-size: 1rem;
	color: #A51C30;
	text-decoration: underline;
}

.same__type{
	font-size: 1.3rem;
	color: #222;
	font-weight: bold;
}
.same__list0{list-style: none}
.same__list0__none{display: none}
.same__list0 a:hover{
	text-decoration: none;
	color: #8A2634;
}
.same__list0 li{
	margin: 6px 0 0 40px;
}
.same__href0{
	color: #A51C30;
	text-decoration: underline;
}
.same__href1{list-style: none}

.same__list1 a{
	text-decoration: underline;
	color: #8A2634;
}
.same__href1{
	color: #222 !important;
	text-decoration: none !important;
}
.same__list1{list-style: none !important}


